import LockOutlinedIcon from '@material-ui/icons/LockOutlined'
import withStyles from '@material-ui/core/styles/withStyles'
import {Link, Redirect, useHistory} from 'react-router-dom'
import {useDispatch, useSelector} from "react-redux";
import {login, setAutorization} from "../../../store/auth-reducer";
import React, {useState} from 'react'
import api from "../../../dataAccessLayer/firebase";
import s from './login.module.scss'

// 
import {Typography, Paper, Avatar, Button, FormControl, Input, InputLabel} from '@material-ui/core'


const styles = theme => ({
    main: {
        width: 'auto',
        display: 'block', // Fix IE 11 issue.
        marginLeft: theme.spacing.unit * 3,
        marginRight: theme.spacing.unit * 3,
        [theme.breakpoints.up(400 + theme.spacing.unit * 3 * 2)]: {
            width: 400,
            marginLeft: 'auto',
            marginRight: 'auto',
        },
    },
    paper: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        padding: `${theme.spacing.unit * 2}px ${theme.spacing.unit * 3}px ${theme.spacing.unit * 3}px`,
    },
    avatar: {
        margin: theme.spacing.unit,
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing.unit,
    },
    submit: {
        marginTop: theme.spacing.unit * 3,
    },
});

const Login = ({classes,closeLoginModal}) => {
    const history = useHistory()
    const dispatch = useDispatch()
    const isAuth = useSelector(state => state.auth.isAuth)
    const [showRegister, setShowRegister] = useState(false)
    const [inputInfo, setInputInfo] = useState({})


    const show = () => {
        setShowRegister(true)
    }
    const close = () => {
        setShowRegister(false)
    }


    const inputHandler = e => {
        setInputInfo({...inputInfo, [e.target.id]: e.target.value})
    }


    const loginWithEmailAndPassword = async () => {
        try {
            const response = await api.login(inputInfo.email, inputInfo.password)
            if (response) {
                dispatch(login(response.user.uid, response.user.displayName))
                closeLoginModal()
            }
        } catch (error) {
            alert(error.message)
        }
    }

    const onRegister = async () => {
        try {
            await api.register(inputInfo.name, inputInfo.email, inputInfo.password)
            const user = await api.isAutorized()
            if (user) {
                const {uid, displayName} = user
                dispatch(setAutorization(uid, displayName))
            }
            closeLoginModal()

        } catch (error) {
            alert(error.message)
        }
    }


    if (isAuth) return <Redirect to={"/"}/>
    return (

            <div className={classes.main}>
                <Paper className={classes.paper}>
                    <Avatar className={classes.avatar}>
                        <LockOutlinedIcon/>
                    </Avatar>
                    <Typography component="h1" variant="h5">
                        {showRegister ? '' : ''}
                    </Typography>
                    <form className={classes.form}
                          onSubmit={e => e.preventDefault() && false}>
                        {showRegister &&
                        <FormControl margin="normal" required fullWidth>
                            <InputLabel htmlFor="name">Name</InputLabel>
                            <Input id="name"
                                   name="name"
                                   autoComplete="off"
                                   autoFocus
                                   value={inputInfo.name}
                                   onChange={inputHandler}/>
                        </FormControl>
                        }
                        <FormControl margin="normal"
                                     required
                                     fullWidth>
                            <InputLabel htmlFor="email">Email Address</InputLabel>
                            <Input id="email"
                                   name="email"
                                   autoComplete="off"
                                   autoFocus
                                   value={inputInfo.email}
                                   onChange={inputHandler}/>
                        </FormControl>
                        <FormControl margin="normal"
                                     required
                                     fullWidth>
                            <InputLabel htmlFor="password">Password</InputLabel>
                            <Input name="password"
                                   type="password"
                                   id="password"
                                   autoComplete="off"
                                   value={inputInfo.password}
                                   onChange={inputHandler}/>
                        </FormControl>

                        <Button
                            type="submit"
                            fullWidth
                            variant="contained"
                            color="primary"
                            onClick={showRegister ? onRegister : loginWithEmailAndPassword}
                            className={classes.submit}>
                            {showRegister ? '' : ''}
                        </Button>
                        <Button
                            type="submit"
                            fullWidth
                            variant="contained"
                            color="secondary"
                            onClick={showRegister ? close : show}
                            className={classes.submit}>
                            {showRegister ? '' : ''}
                        </Button>
                    </form>
                </Paper>
            </div>

    )
}

export const LoginForm = withStyles(styles)(Login)