import s from "./AuthForm.module.scss";
import {Link} from "react-router-dom";
import React, {Fragment, useState} from "react";

export const AuthForm = () => {

    const [showRegister, setShowRegister] = useState(false)
    const show = () => {setShowRegister(true)}
    const close = () => {setShowRegister(false)}
    return (
        <div className={s.auth}>
            <div className={s.auth__title}>  </div>
            <div className={s.auth__widget}>
                <div className={s.auth__row}>
                    <form>
                        {showRegister &&
                        <div className={s.auth__content}>
                            <input type="text"
                                   placeholder=""
                                   className={s.auth__input}/>
                        </div>
                        }
                        <div className={s.auth__content}>
                            <input type="text"
                                   placeholder="Email  id"
                                   className={s.auth__input}/>
                        </div>
                        <div className={s.auth__content}>
                            <input type="password"
                                   placeholder=""
                                   className={s.auth__input}/>
                        </div>
                        {showRegister
                            ? <Fragment>
                                <div type="button" className={s.auth__button}>
                                    <span className={s.auth__authentication}></span>
                                </div>
                                <div type="button" className={s.auth__button} onClick={close}>
                                    <span className={s.auth__authentication}></span>
                                </div>
                            </Fragment>
                            : <Fragment>
                                <div type="button" className={s.auth__button}>
                                    <span className={s.auth__authentication}></span>
                                </div>
                                <div type="button" className={s.auth__button} onClick={show}>
                                    <span className={s.auth__authentication}></span>
                                </div>
                            </Fragment>
                        }
                    </form>
                </div>
                <div className={s.auth__container}>
                    <Link className={s.auth__link} to='/contacts'> ?</Link>
                </div>
            </div>
        </div>
    )
}